import { _decorator, Component, Node, tween, Vec3 } from 'cc';
import { Piece } from '../Piece/Piece';
import { GridManager } from '../Grid/GridManager';
import { GameGlobal } from '../Game/GameGlobal';
const { ccclass, property } = _decorator;

@ccclass('GravityHandler')
export class GravityHandler extends Component {

    async applyGravity() {
        const grid = GridManager.getInstance().grid;
        const rows = grid.length;
        const cols = grid[0].length;

        // Grid'i dolaş
        for (let col = 0; col < cols; col++) {
            for (let row = rows - 2; row >= 0; row--) {  // En alt satırdan bir üstünden başlayarak yukarı doğru git
                let piece = grid[row][col];

                // Eğer hücre boşsa devam et
                if (!piece || piece.node === null) continue;

                let newRow = row;

                // Altındaki hücre boş olduğu sürece bu işlemi yap
                while (newRow + 1 < rows && !grid[newRow + 1][col].node) {
                    newRow++;
                }

                // Eğer yeni bir pozisyon bulduysak, taşı
                if (newRow !== row) {
                    grid[newRow][col] = piece;  // Yeni pozisyona taşı
                    grid[row][col] = null;  // Eski pozisyonu boşalt

                    piece.row = newRow;  // Yeni satırı güncelle
                    await this.movePiece(piece, newRow, col);  // Parçayı hareket ettir
                }
            }
        }
    }

    movePiece(piece: Piece, newRow: number, col: number): Promise<void> {
        const newY = newRow * (GameGlobal.PIECE_CONTENT_SIZE + GameGlobal.PIECE_OFFSET);

        return new Promise<void>((resolve) => {
            tween(piece.node)
                .to(0.3, { position: new Vec3(piece.node.position.x, newY, 0) })
                .call(() => resolve())
                .start();
        });
    }
}
